@each $breakpoint in map-keys($grid-breakpoints){
  @include media-breakpoint-up($breakpoint){
    $infix:breakpoint-infix($breakpoint, $grid-breakpoints);

    .dds__d#{$infix}-none{ display:none !important; }
    .dds__d#{$infix}-inline{ display:inline !important; }
    .dds__d#{$infix}-inline-block{ display:inline-block !important; }
    .dds__d#{$infix}-block{ display:block !important; }
    .dds__d#{$infix}-table{ display:table !important; }
    .dds__d#{$infix}-table-row{ display:table-row !important; }
    .dds__d#{$infix}-table-cell{ display:table-cell !important; }
    .dds__d#{$infix}-flex{ display:flex !important; }
    .dds__d#{$infix}-inline-flex{ display:inline-flex !important; }
  }
}

@media print{
  .dds__d-print-none{ display:none !important; }
  .dds__d-print-inline{ display:inline !important; }
  .dds__d-print-inline-block{ display:inline-block !important; }
  .dds__d-print-block{ display:block !important; }
  .dds__d-print-table{ display:table !important; }
  .dds__d-print-table-row{ display:table-row !important; }
  .dds__d-print-table-cell{ display:table-cell !important; }
  .dds__d-print-flex{ display:flex !important; }
  .dds__d-print-inline-flex{ display:inline-flex !important; }
}
