@each $breakpoint in map-keys($grid-breakpoints){
  @include media-breakpoint-up($breakpoint){
    $infix:breakpoint-infix($breakpoint, $grid-breakpoints);

    .dds__flex#{$infix}-row{ flex-direction:row !important; }
    .dds__flex#{$infix}-column{ flex-direction:column !important; }
    .dds__flex#{$infix}-row-reverse{ flex-direction:row-reverse !important; }
    .dds__flex#{$infix}-column-reverse{ flex-direction:column-reverse !important; }

    .dds__flex#{$infix}-wrap{ flex-wrap:wrap !important; }
    .dds__flex#{$infix}-nowrap{ flex-wrap:nowrap !important; }
    .dds__flex#{$infix}-wrap-reverse{ flex-wrap:wrap-reverse !important; }
    .dds__flex#{$infix}-fill{ flex:1 1 auto !important; }
    .dds__flex#{$infix}-grow-0{ flex-grow:0 !important; }
    .dds__flex#{$infix}-grow-1{ flex-grow:1 !important; }
    .dds__flex#{$infix}-shrink-0{ flex-shrink:0 !important; }
    .dds__flex#{$infix}-shrink-1{ flex-shrink:1 !important; }

    .dds__justify-content#{$infix}-start{ justify-content:flex-start !important; }
    .dds__justify-content#{$infix}-end{ justify-content:flex-end !important; }
    .dds__justify-content#{$infix}-center{ justify-content:center !important; }
    .dds__justify-content#{$infix}-between{ justify-content:space-between !important; }
    .dds__justify-content#{$infix}-around{ justify-content:space-around !important; }

    .dds__align-items#{$infix}-start{ align-items:flex-start !important; }
    .dds__align-items#{$infix}-end{ align-items:flex-end !important; }
    .dds__align-items#{$infix}-center{ align-items:center !important; }
    .dds__align-items#{$infix}-baseline{ align-items:baseline !important; }
    .dds__align-items#{$infix}-stretch{ align-items:stretch !important; }

    .dds__align-content#{$infix}-start{ align-content:flex-start !important; }
    .dds__align-content#{$infix}-end{ align-content:flex-end !important; }
    .dds__align-content#{$infix}-center{ align-content:center !important; }
    .dds__align-content#{$infix}-between{ align-content:space-between !important; }
    .dds__align-content#{$infix}-around{ align-content:space-around !important; }
    .dds__align-content#{$infix}-stretch{ align-content:stretch !important; }

    .dds__align-self#{$infix}-auto{ align-self:auto !important; }
    .dds__align-self#{$infix}-start{ align-self:flex-start !important; }
    .dds__align-self#{$infix}-end{ align-self:flex-end !important; }
    .dds__align-self#{$infix}-center{ align-self:center !important; }
    .dds__align-self#{$infix}-baseline{ align-self:baseline !important; }
    .dds__align-self#{$infix}-stretch{ align-self:stretch !important; }
  }
}
